	async getWeb(relUrl, opts = {}) {
		let {
			viewFields,
			isCached
		} = opts;
		if (!this.getWeb.cache) this.getWeb.cache = {};
		let cache = this.getWeb.cache[relUrl];
		if (isCached && cache) {
			return cache;
		} else {
			let clientContext = new SP.ClientContext(relUrl);
			let web = clientContext.get_web();
			this.load(clientContext, web, viewFields);
			await this.execute(clientContext);
			this.requestsCount.get.web++;
			let preparedResponse = this.prepareResponse(web, opts);
			isCached && (this.getWeb.cache[relUrl] = preparedResponse);
			return preparedResponse;
		}
	}

	async createWeb(webPath, opts) {
		let webSplits = webPath.split('/');
		let webTitle = webSplits.pop();
		let clientContext = new SP.ClientContext(webSplits.join('/') || '/');
		let web = clientContext.get_web();
		let webCreationInfo = new SP.WebCreationInformation();
		webCreationInfo.set_title(webTitle);
		webCreationInfo.set_language(1033);
		webCreationInfo.set_url(webTitle);
		webCreationInfo.set_useSamePermissionsAsParentSite(true);
		webCreationInfo.set_webTemplate('STS');
		web.get_webs().add(webCreationInfo);
		web.update();
		await this.execute(clientContext);
		this.requestsCount.create.web++;
		console.log(`created web: ${webPath}`);
		return this.prepareResponse(web, opts);
	}

	async deleteWeb(webPath) {
		let clientContext = new SP.ClientContext(webPath);
		clientContext.get_web().deleteObject();
		await this.execute(clientContext);
		this.requestsCount.delete.web++;
		console.log(`deleted web: ${webPath}`);
	}

