	async getColumn(listData, namesOrTitlesOrGuids, opts = {}) {
		let nameOrTitleOrGuid, cache;
		let columnsCollection = {};
		let columnsToQuery = {};
		typeOf(listData) === 'string' && (listData = this.getListData(listData));
		let {
			viewFields,
			isCached
		} = opts;
		if (typeOf(namesOrTitlesOrGuids) !== 'array') namesOrTitlesOrGuids = [namesOrTitlesOrGuids];
		let clientContext = new SP.ClientContext(listData.path);
		let list = this.getListSPObject(clientContext, listData.title);
		let fields = list.get_fields();
		if (!this.getColumns.cache) this.getColumns.cache = {};
		for (let i = 0, cache; i < namesOrTitlesOrGuids.length; i++) {
			nameOrTitleOrGuid = namesOrTitlesOrGuids[i];
			cache = this.getColumns.cache[listData.str];
			if (isCached && cache[nameOrTitleOrGuid]) {
				columnsCollection[nameOrTitleOrGuid] = cache;
			} else {
				let field = this.constants.GUID_MASK.test(nameOrTitleOrGuid) ?
					fields.getById(nameOrTitleOrGuid) :
					fields.getByInternalNameOrTitle(nameOrTitleOrGuid);
				this.load(clientContext, field, viewFields);
				columnsToQuery[nameOrTitleOrGuid] = field;
			}
		}
		if (Object.keys(columnsToQuery).length) {
			await this.execute(clientContext);
			let preparedResponse = {};
			this.requestsCount.get.column++;
			for (let columnName in columnsToQuery) {
				preparedResponse[columnName] = this.prepareResponse(columnsToQuery[columnName], opts);
			}
			columnsCollection = Object.assign(columnsToQuery, preparedResponse);
			isCached && Object.assign(this.getColumns.cache[listData.str], columnsCollection);
		}
		return columnsCollection;
	}

	async createColumn(listData, columnParams) {
		let columnItem, column, columnXML;
		typeOf(listData) === 'string' && (listData = this.getListData(listData));
		let clientContext = new SP.ClientContext(listData.path);
		let fields = this.getListSPObject(clientContext, listData.title).get_fields();
		if (typeOf(columnParams) !== 'array') columnParams = [columnParams];
		for (let i = 0; i < columnParams.length; i++) {
			column = columnParams[i];
			if (!column.title) continue;
			columnXML = fields.addFieldAsXml(`
					<Field Type="${column.type}" 
						DisplayName="${column.title} + '" 
						Name="${column.title} + '" 
						Required="${(column.required ? 'True' : 'False')}" 
						Group="${column.group}"
					/>`, true, SP.AddFieldOptions.defaultValue);
			switch (column.type) {
				case 'Text':
					columnItem = clientContext.castTo(columnXML, SP.FieldText);
					break;
				case 'Note':
					columnItem = clientContext.castTo(columnXML, SP.FieldMultiLineText);
					columnItem.set_richText(column.isRichText || false);
					break;
				case 'Number':
					columnItem = clientContext.castTo(columnXML, SP.FieldNumber);
					break;
				case 'Boolean':
					columnItem = clientContext.castTo(columnXML, SP.Field);
					break;
				case 'Choice':
					columnItem = clientContext.castTo(columnXML, column.isMultiple ? SP.FieldMultiChoice : SP.FieldChoice);
					break;
				case 'DateTime':
					columnItem = clientContext.castTo(columnXML, SP.FieldDateTime);
					break;
				case 'Lookup':
				case 'LookupMulti':
					columnItem = clientContext.castTo(columnXML, SP.FieldLookup);
					column.lookupWebGuid && columnItem.set_lookupWebId(column.lookupWebGuid);
					columnItem.set_lookupList(column.lookupListGuid);
					columnItem.set_lookupField(column.lookupField || 'Title');
					column.isMultiple && columnItem.set_allowMultipleValues(true);
					break;
				case 'User':
				case 'UserMulti':
					columnItem = clientContext.castTo(columnXML, SP.FieldUser);
					column.isMultiple && columnItem.set_allowMultipleValues(true);
					break;
			}
			if (columnItem) {
				columnItem.set_defaultValue(column.defaultValue);
				columnItem.update();
			}
		}
		if (columnXML) {
			await this.execute(clientContext);
			this.requestsCount.create.column++;
			console.log('created columns: ' + columns.length);
		} else {
			console.log('nothing to create');
		}
	}

