	async mergeColumns(listData, sourceColumnName, targetColumnName, opts = {}) {
		let currentColumnData, currentColumnValue, sourceColumn, targetColumn;
		let {
			caml,
			folder,
			mediator,
			mode = 'merge'
		} = opts;
		let columnsData = await this.getColumn(listData, [sourceColumnName, targetColumnName], {
			mapBy: 'InternalName'
		});
		let sourceColumnType = columnsData[sourceColumnName].TypeAsString;
		let targetColumnType = columnsData[targetColumnName].TypeAsString;
		let columnItemsData = await this.getItems(listData, {
			folder: folder,
			recursive: 'All',
			caml: `${sourceColumnType} ${sourceColumnName} IsNotNull`
		});
		let columnsToMerge = [];
		for (let i = 0; i < columnItemsData.length; i++) {
			columnItemData = columnItemsData[i];
			sourceColumn = columnItemData[sourceColumnName];
			targetColumn = columnItemData[targetColumnName];
			if (mode === 'merge' && targetColumn !== null && targetColumn && targetColumn.length) continue;
			currentColumnData = {};
			currentColumnValue = {};
			currentColumnValue[targetColumnName] = mediator ? mediator(sourceColumn) : sourceColumn;
			currentColumnData[columnItemData.ID] = currentColumnValue;
			columnsToMerge.push(currentColumnData);
		}
		if (columnsToMerge.length) {
			return await this.updateItem(listData, null, columnsToMerge);
		} else {
			console.log('nothing to merge');
		}
	}

