	async createFolderByUrl(webRelUrl, folder, opts) {
		let childFolders = folder.toString().split('/');
		let rootFolderTitle = childFolders.shift();
		let parentFolders = [rootFolderTitle];
		createFolderR = createFolderR.bind(this);
		return await createFolderR(rootFolderTitle, childFolders);

		async function createFolderR(parentFolderTitle, childFolders) {
			let foldersData = await this.getRootFoldersByUrl(webRelUrl, {
				isExpanded: true
			})
			let folder = foldersData.find(folder => {
				return folder.get_name().toLowerCase() === parentFolderTitle.toLowerCase() ? folder : null
			})
			if (folder) {
				console.log(childFolders);
				if (childFolders.length) {
					parentFolderTitle = childFolders.shift();
					parentFolders.push(parentFolderTitle);
					return await createFolderR(parentFolderTitle, childFolders);
				} else {
					console.log(`folder already exists: ${webRelUrl}/${parentFolderTitle}`);
					return this.prepareResponse(folder, opts);
				}
			} else {
				let clientContext = new SP.ClientContext(webRelUrl);
				let folders = clientContext.get_web().get_folders();
				folders.add(parentFolderTitle);
				clientContext.load(folders);
				await this.execute(clientContext);
				this.requestsCount.create.folder++;
				if (childFolders.length) {
					parentFolderTitle = childFolders.shift();
					parentFolders.push(parentFolderTitle);
					return await createFolderR(parentFolderTitle, childFolders);
				} else {
					console.log(`created folder : ${folder}, ${webRelUrl}`);
				}
			}
		}
	}

