	async createItemIndex(listData, itemData) {
		if (this.getListIndex(listData)) {
			let dataToCreate = [];
			for (let i = 0; i < itemData.length; i++) {
				dataToCreate.push(getIndexColumnsToCreate(itemData[i]));
			}
			return await this.createItems(this.searchList, dataToCreate)
		}

		function getIndexColumnsToCreate(itemData) {
			let columnName, itemValue;
			let item = itemData.get_fieldValues();
			let iDataItems = [];
			if (indexItem.data && indexItem.data.length) {
				for (let i = 0; i < indexItem.data.length; i++) {
					let iDataItem = item[indexItem.data[i]];
					if (iDataItem) {
						iDataItems.push(iDataItem)
					}
				}
			}
			let columns = {
				Title: 'search',
				date: new Date().getTime(),
				iAuthor: item.Author.get_lookupId(),
				iID: item.ID,
				iHeading: item[indexItem.heading],
				iType: listData,
				iData: iDataItems.join(' '),
				iDate: item.Created.getTime(),
				iLink: indexItem.fullLink ? indexItem.link : (`http://aura.dme.aero.corp/app-pages/${indexItem.link}`)
			}
			for (let i = 0; i < indexItem.get.length; i++) {
				columnName = indexItem.get[i];
				itemValue = item[columnName];
				columns.iLink = columns.iLink.replace(new RegExp('\{' + columnName + '\}', 'g'), itemValue.get_lookupId ? itemValue.get_lookupId() : itemValue)
			}
			return columns;
		}
	}

	async updateItemIndex(listData, updatedItemData) {
		let columnToUpdate;
		let indexItem = this.getListIndex(listData);
		let ids = [];
		if (typeOf(updatedItemData) !== 'array') updatedItemData = [updatedItemData];
		for (let i = 0; i < updatedItemData.length; i++) ids.push(updatedItemData[i].get_id());
		if (indexItem) {
			let indexData = this.getItems(this.searchList, {
				isExpanded: true,
				mapBy: 'iID',
				caml: `${iType} Eq ${indexItem.query.encode()} And Number iID Eq ${ids}`
			})
			let currentItem, item;
			let columnsToUpdate = [];
			let itemsToCreate = [];
			for (let i = 0; i < updatedItemData.length; i++) {
				currentItem = indexData[item.get_id()];
				if (currentItem) {
					columnToUpdate = this.getIndexColumnsToUpdate(item, currentItem);
					columnToUpdate && columnsToUpdate.push(columnToUpdate);
				} else {
					itemsToCreate.push(updatedItemData)
				}
			}
			await this.createItemIndex(listData, itemsToCreate)
			await this.updateItem(this.searchList, columnsToUpdate);
		}

		function getIndexColumnsToUpdate(itemData, indexData) {
			let columns, columnToUpdate;
			let item = itemData.get_fieldValues();
			if (item.Modified.getTime() > indexData.Modified.getTime() && item[indexItem.heading] !== indexData.iHeading) {
				columnToUpdate = {};
				columns = {
					iHeading: itemData[indexItem.heading]
				}
				columnToUpdate[indexData.ID] = columns;
			}
			return columnToUpdate;
		}
	}

