	async uploadFile(listData, fileData, folder) {
		typeOf(listData) === 'string' && (listData = this.getListData(listData));
		if (fileData && typeOf(fileData) === 'string') {
			fileData = {
				base64: '',
				filename: fileData
			}
		}

		if (folder) folderData = await this.createFolder(listData, folder)
		let relFolder, clientContext;
		let out = '';
		let createInfo = new SP.FileCreationInformation;
		if (folderData) {
			clientContext = folderData.get_context();
			relFolder = folderData.get_folder ? folderData.get_folder() : folderData;
		} else {
			clientContext = new SP.ClientContext(listData.path);
			relFolder = this.getListSPObject(clientContext, listData.title).get_rootFolder()
		}
		if (fileData.hasOwnProperty('base64')) {
			createInfo.set_content(fileData.base64);
		} else if (fileData.hasOwnProperty('arrayBuffer')) {
			let bytes = new Uint8Array(fileData.arrayBuffer);
			for (let i = 0, length = bytes.length; i < length; i += 1) out += String.fromCharCode(bytes[i]);
			createInfo.set_content(btoa(out));
		} else {
			console.log('no converter for file: ');
			console.log(fileData);
			return;
		}
		createInfo.set_overwrite(true);
		createInfo.set_url(fileData.filename);
		let newFile = relFolder.get_files().add(createInfo);
		let listItem = newFile.get_listItemAllFields();
		if (fileData.columns) {
			for (let column in fileData.columns) {
				listItem.set_item(column, fileData.columns[column]);
			}
			listItem.update();
		}
		clientContext.load(listItem);
		clientContext.load(newFile);
		await this.execute(clientContext);
		this.requestsCount.create.file++;
		console.log(`uploaded file: ${(fileData.columns ? fileData.columns.Title : fileData.filename)}, ${this.formatFolderRelUrl(listData, folder, 1)}/${fileData.filename}`);
		return [listItem.get_id(), newFile];
	}

