	async uploadFileREST(listData, formData, opts = {}) {
		let onProgress = opts.onProgress || function() {};
		typeOf(listData) === 'string' && (listData = this.getListData(listData));
		let list = await this.getList(listData, {
			isCached: true
		});
		let res = await axios.post(listData.path + '/_layouts/15/UploadEx.aspx?List={' + list.Id + '}', formData, {
			cache: false,
			headers: {
				'Upgrade-Insecure-Requests': 1,
				'Cache-Control': 'max-age=0',
				'Accept': 'text/html,application/xhtml+xml,application/xml;q=0.9,image/webp,image/apng,*/*;q=0.8'
			},
			contentType: false,
			processData: false,
			onUploadProgress: function(e) {
				e.lengthComputable && onProgress(parseFloat((e.loaded / e.total * 100).toFixed(2)));
			},
		})
		return res.data;
	}

	async getFileREST(listData, filename, opts = {}) {
		let headers = {};
		typeOf(listData) === 'string' && (listData = this.getListData(listData));
		let {
			folder,
			isRaw,
			needValue
		} = opts;
		if (!needValue && !isRaw) {
			headers = {
				Accept: 'application/json;odata=verbose'
			}
		}
		let res = await axios.get(`${listData.path}/_api/web/getfilebyserverrelativeurl('${listData.path || ''}/${listData.title}/${(folder ? `${folder}/` : '') + filename}')${(needValue ? '/$value' : '')}`, {
			...headers,
			binaryStringResponseBody: true
		})
		return isRaw || needValue ? res : res.d;
	}

