	async copyFileREST(sourceListData, targetListData, sourceFileName, targetFileName, opts = {}) {
		let {
			sourceFolder,
			targetFolder
		} = opts;
		typeOf(sourceListData) === 'string' && (sourceListData = this.getListData(sourceListData));
		typeOf(targetListData) === 'string' && (targetListData = this.getListData(targetListData));
		let sourceUrl = `${sourceListData.path}/${sourceListData.title}/${(sourceFolder ? `${sourceFolder}/` : '') + sourceFileName}`;
		let targetUrl = `${targetListData.path}/${targetListData.title}/${(targetFolder ? `${targetFolder}/` : '') + targetFileName}`;
		let res = await axios.post(`${sourceListData.path}/_api/web/getfilebyserverrelativeurl('${sourceUrl}')/copyTo('${targetUrl}')`, null, {
			contentType: "application/json;odata=verbose",
			headers: {
				'Accept': 'application/json;odata=verbose',
				'X-RequestDigest': await this.getRequestDigestREST()
			},
		})
		console.log(`copied file: from ${sourceUrl} to ${targetUrl}`);
		return res
	}

	async copyOrMoveFileREST(sourceListData, targetListData, sourceFileName, targetFileName, opts = {}) {
		let {
			isExpanded,
			sourceFolder,
			isMove
		} = opts;
		let fileData = await this.getFileREST(sourceListData, sourceFileName, {
			folder: sourceFolder,
			isRaw: true,
			needValue: true
		})
		opts.content = fileData.body;
		let res = await this.uploadFileREST(targetListData, targetFileName, opts)
		if (isMove) {
			await this.deleteFileREST(sourceListData, sourceFileName, {
				noRecycle: true
			});
		}
		return res;
	}

