	async restoreAllListItems(listData) {
		typeOf(listData) === 'string' && (listData = this.getListData(listData));
		let clientContext = new SP.ClientContext(listData.path);
		let itemsData = await this.getAllRecycledItems(listData.path, {
			isExpanded: true
		})
		let restoreChunks = this.chunkItems(itemsData);
		restoreR = restoreR.bind(this);
		restoreR(restoreChunks);

		async function restoreR(restoreChunks) {
			let itemContext;
			if (restoreChunks.length) {
				let chunk = restoreChunks.shift();
				for (let i = 0; i < chunk.length; i++) {
					let itemData = chunk[i];
					itemContext = itemData.get_context();
					itemData.restore();
					itemContext.load(itemData);
				}
				await this.execute(itemContext);
				console.log('restored items: ' + chunk.length);
				restoreR(restoreChunks);
			} else {
				console.log('restore complete');
			}
		}
	}

	async clearRecycleBin(relPath) {
		let clientContext = new SP.ClientContext(relPath);
		let recycleItemCollection = clientContext.get_web().get_recycleBin();
		recycleItemCollection.deleteAll();
		clientContext.load(recycleItemCollection);
		await this.execute(clientContext);
	}

