const IN_CHUNK_SIZE = 500;
const IN_CUSTOM_DELIMETER = '8^)';
const GROUP_REGEXP_STR = '\\s(and|or)\\s';
const GROUP_REGEXP = new RegExp(GROUP_REGEXP_STR, 'i');
const TIME_STAMP_ISO_REGEXP = /^\d\d\d\d-\d\d-\d\dT\d\d\:\d\d\:\d\d(\.\d\d\d)?Z$/;
const COLUMN_TYPES = {
	AppAuthor: 'Lookup',
	AppEditor: 'Lookup',
	Attachments: 'Attachments',
	Author: 'Lookup',
	BaseName: 'Computed',
	ContentType: 'Computed',
	ContentTypeId: 'ContentTypeId',
	Created: 'DateTime',
	Created_x0020_Date: 'Lookup',
	DocIcon: 'Computed',
	Edit: 'Computed',
	Editor: 'Lookup',
	EncodedAbsUrl: 'Computed',
	FSObjType: 'Number',
	FileDirRef: 'Lookup',
	FileLeafRef: 'File',
	FileRef: 'File',
	File_x0020_Type: 'Text',
	FolderChildCount: 'Number',
	GUID: 'Guid',
	HTML_x0020_Type: 'Computed',
	ID: 'Counter',
	InstanceID: 'Integer',
	ItemChildCount: 'Number',
	Last_x0020_Modified: 'Lookup',
	LinkFilename: 'Computed',
	LinkFilename2: 'Computed',
	LinkFilenameNoMenu: 'Computed',
	LinkTitle: 'Computed',
	LinkTitle2: 'Computed',
	LinkTitleNoMenu: 'Computed',
	MetaInfo: 'Lookup',
	Modified: 'DateTime',
	Order: 'Number',
	PermMask: 'Computed',
	ProgId: 'Lookup',
	ScopeId: 'Lookup',
	SelectTitle: 'Computed',
	ServerUrl: 'Computed',
	SortBehavior: 'Lookup',
	SyncClientId: 'Lookup',
	Title: 'Text',
	UniqueId: 'Lookup',
	WorkflowInstanceID: 'Guid',
	WorkflowVersion: 'Integer',
	_CopySource: 'Text',
	_EditMenuTableEnd: 'Computed',
	_EditMenuTableStart: 'Computed',
	_EditMenuTableStart2: 'Computed',
	_HasCopyDestinations: 'Boolean',
	_IsCurrentVersion: 'Boolean',
	_Level: 'Integer',
	_ModerationComments: 'Note',
	_ModerationStatus: 'ModStat',
	_UIVersion: 'Integer',
	_UIVersionString: 'Text'
}
const COLUMN_TYPES_REGEXP = /(Text|Note|Number|Integer|Counter|Boolean|Lookup|User|DateTime|Date|Time|Calculated|Currency|ModStat|Guid|File|Attachments|LookupMulti|UserMulti)\s/i;
const OPERATORS_REGEXP = /\s(eq|neq|geq|leq|gt|lt|isnull|isnotnull|contains|beginsWith|includes|notincludes|search|in)(\s|$)/i;
const COLUMN_TYPES_MAPPED = {
	text: 'Text',
	note: 'Text',
	number: 'Number',
	integer: 'Text',
	counter: 'Text',
	boolean: 'Text',
	lookup: 'Text',
	user: 'Text',
	datetime: 'Text',
	date: 'Text',
	time: 'Text',
	currency: 'Text',
	calculated: 'Text',
	lookupmulti: 'Text',
	usermulti: 'Text',
	modstat: 'ModStat',
	guid: 'Guid',
	file: 'File',
	attachments: 'Attachments'
}
const OPERATORS_MAPPED = {
	eq: 'Eq',
	neq: 'Neq',
	geq: 'Geq',
	leq: 'Leq',
	gt: 'Gt',
	lt: 'Lt',
	isnull: 'IsNull',
	isnotnull: 'IsNotNull',
	beginswith: 'BeginsWith',
	contains: 'Contains',
	includes: 'Includes',
	notincludes: 'NotIncludes',
	search: 'Search',
	guid: 'Guid',
	file: 'File',
	attachments: 'Attachments',
	lookupmulti: 'LookupMulti',
	usermulti: 'UserMulti',
	search: 'Search',
	in: 'In'
}
const GROUP_OPERATORS_MAPPED = {
	and: 'And',
	or: 'Or'
}

function getQuery(str) {
	if (!str) return '';
	if (typeOf(str) === 'object') {
		return getCamlQuery(str);
	} else {
		let sanitaizedStr = str.replace(/^\s+/, '');
		sanitaizedStr = trimBraces(sanitaizedStr);
		if (/^\<|\>$/.test(sanitaizedStr)) return str;
		return GROUP_REGEXP.test(sanitaizedStr) ? convertGroupR(sanitaizedStr) : convertExpression(sanitaizedStr)
	}
}

function getView(str, params = {}) {
	let item, itemSplits;
	let orderByStr = '';
	let queryStr = '';
	let limitStr = '';
	let recursiveStr = '';
	let {
		orderBy,
		recursive,
		limit
	} = params;
	if (orderBy) {
		if (typeOf(orderBy) !== 'array') {
			orderBy = [orderBy];
		}
		for (var i = 0; i < orderBy.length; i++) {
			item = orderBy[i];
			itemSplits = item.split(':');
			orderByStr += `<FieldRef Name="${itemSplits[0]}"${/desc/i.test(itemSplits[1]) ? ' Ascending="False"' : ''}/>`
		}
		orderByStr = `<OrderBy>${orderByStr}</OrderBy>`;
	}
	if (recursive) recursiveStr = ` Scope="Recursive${/all/i.test(recursive) ? 'All"' : '"'}`;
	if (orderByStr) {
		queryStr = `<Query>${(str ? `<Where>${getQuery(str)}</Where>${orderByStr}`:`${orderByStr}`)}</Query>`;
	} else {
		if (str) queryStr = `<Query><Where>${getQuery(str)}</Where></Query>`;
	}
	if (limit) limitStr = `<RowLimit>${limit}</RowLimit>`;
	return recursiveStr || queryStr || limitStr ? `<View${recursiveStr}>${queryStr + limitStr}</View>` : '';
}

function trimBraces(str) {
	if (str[0] === '(' && str[str.length - 1] === ')') {
		return getStringFromOuterBraces(str);
	};
	return str;
}

function camlLog(str) {
	console.log(str && DOMParser ? new DOMParser().parseFromString(str, 'text/xml') : str);
};

function convertExpression(str) {
	let operator, columnSplits, operatorMatch, fieldOption, includeTimeValue;
	let chunks = [];
	let valueStrings = [];
	let values = [];
	let valueChunks = [];
	let type = 'text';
	let operatorMatches = str.match(OPERATORS_REGEXP);
	if (operatorMatches) {
		operatorMatch = operatorMatches[0];
		operator = operatorMatches[1].toLowerCase();
	} else {
		throw new SyntaxError(`Wrong operator in "${str}"`);
	}
	let strSplits = str.split(new RegExp(operatorMatch, 'i'));
	let columnStr = strSplits[0].replace(/\s+/g, ' ').trim();
	let name = columnStr;
	if (COLUMN_TYPES_REGEXP.test(columnStr)) {
		columnSplits = columnStr.split(' ');
		type = columnSplits.shift().toLowerCase();
		name = columnSplits.join(' ');
	}
	let value = strSplits[1];
	if (/\^$/.test(name)) {
		name = name.substring(0, name.length - 1);
		fieldOption = 'LookupId="True"';
	}
	switch (type) {
		case 'datetime':
		case 'time':
		case 'date':
			includeTimeValue = 'StorageTZ="True"';
			if (type !== 'date') {
				includeTimeValue += ' IncludeTimeValue="True"';
			}
			type = 'datetime'
			break;
		case 'text':
			if (TIME_STAMP_ISO_REGEXP.test(value)) {
				includeTimeValue = 'StorageTZ="True"';
			}
			break;
	}
	if (type !== 'text') {
		value = value.trim();
	}
	switch (operator) {
		case 'in':
			value = value.split(value.indexOf(IN_CUSTOM_DELIMETER) >= 0 ? IN_CUSTOM_DELIMETER : ',');
			break;
		case 'search':
			let split;
			let searchQuery = 'ID isnull';
			value = value.replace(/\s+/g, ' ').trim();
			let valueSplits = value.split(' ');
			for (let i = 0; i < valueSplits.length; i++) {
				split = valueSplits[i];
				searchQuery = `(${type} ${name} contains ${split} or ${searchQuery})`
			}
			searchQuery = trimBraces(searchQuery);
			return convertGroupR(searchQuery);
			break;
	}
	let operatorNorm = OPERATORS_MAPPED[operator];
	let typeNorm = COLUMN_TYPES_MAPPED[type];
	let fieldOpts = fieldOption !== void 0 ? ` ${fieldOption}` : '';
	let valueOpts = includeTimeValue !== void 0 ? ` ${includeTimeValue}` : '';
	if (operator === 'in') {
		for (let i = 0, length = value.length; i < length; i++) {
			valueStrings.push(`<Value Type="${typeNorm}"${valueOpts}>${value[i]}</Value>`);
		}
		if (value.length > IN_CHUNK_SIZE) {
			chunks = chunkItems(valueStrings, IN_CHUNK_SIZE);
			for (let i = chunks.length - 1; i >= 0; i--) {
				valueChunks.push(`<In><Values>${chunks[i].join('')}</Values></In>`);
			}
			let itemsStr = '<IsNull><FieldRef Name="ID"/></IsNull>';
			for (let i = 0; i < valueChunks.length; i++) {
				itemsStr = `<Or>${valueChunks[i]}${itemsStr}</Or>`
			}
			return values;
		} else {
			values = valueStrings.join('');
			return `<${operatorNorm}><FieldRef Name="${name}"${fieldOpts}/><Values>${values}</Values></${operatorNorm}>`;
		}
	} else {
		return `<${operatorNorm}><FieldRef Name="${name}"${fieldOpts}/>${/null/.test(operator)?'':`<Value Type="${typeNorm}"${valueOpts}>${value}</Value>`}</${operatorNorm}>`
	}
};

function convertGroupR(str) {
	let firstExpr, secondExpr, operator;
	let matches = str.match(new RegExp('^[^\\(]*\\((.*' + GROUP_REGEXP_STR + '.*)\\)[^\\)]*$', 'i'));
	if (matches) {
		let dummyStr = '_dummy_';
		let dummyRE = new RegExp(dummyStr);
		let groupStr = matches[1];
		let dummiedStr = str.replace(groupStr, dummyStr);
		let dummySplits = dummiedStr.split(GROUP_REGEXP);
		let dummySplit0 = dummySplits[0];
		if (dummySplits.length === 3) {
			operator = dummySplits[1];
			let dummySplit2 = dummySplits[2];
			if (dummyRE.test(dummySplit0)) {
				firstExpr = getStringFromOuterBraces(dummySplit0.replace(dummyStr, groupStr));
				secondExpr = dummySplit2;
			} else if (dummyRE.test(dummySplit2)) {
				firstExpr = dummySplit0;
				secondExpr = getStringFromOuterBraces(dummySplit2.replace(dummyStr, groupStr));
			}
		} else {
			return convertExpression(dummySplit0);
		}
	} else {
		let splits = str.split(GROUP_REGEXP);
		firstExpr = splits[0];
		if (splits.length === 3) {
			secondExpr = splits[2];
			operator = splits[1];
		} else {
			return convertExpression(firstExpr);
		}
	}
	let operatorNorm = GROUP_OPERATORS_MAPPED[operator];
	return `<${operatorNorm}>${convertGroupR(firstExpr)}${convertGroupR(secondExpr)}</${operatorNorm}>`;
};

function chunkItems(items, size) {
	let startIndex = 0;
	let chunked = [];
	let chunksCount = Math.ceil(items.length / size);
	for (let chunkIndex = 1; chunkIndex <= chunksCount; chunkIndex++) {
		chunked.push(items.slice(startIndex, startIndex + size))
		startIndex += size;
	}
	return chunked;
};

function getStringFromOuterBraces(str) {
	let founds = str.match(new RegExp('^[^\\(]*\\((.*' + (GROUP_REGEXP.test(str) ? GROUP_REGEXP_STR + '.*' : '') + ')\\)[^\\)]*$', 'i'));
	return founds && founds[1] ? founds[1] : str
};

function concat(items, operator, type, value) {
	let item;
	let query = type === 'and' ? 'ID isnotnull' : 'ID isnull';
	if (typeOf(value) === 'array') {
		let valueLastIndex = value.length - 1;
		for (let i = items.length - 1; i >= 0; i--) {
			item = items[i];
			query = `(${item} ${/null/i.test(operator)?operator:`${operator} ${value[i]||value[valueLastIndex]}`} ${type} ${query})`
		}
	} else {
		for (let i = items.length - 1; i >= 0; i--) {
			item = items[i];
			query = `(${item} ${/null/i.test(operator)?operator:`${operator} ${value}`} ${type} ${query})`
		}
	}
	query = trimBraces(query);
	return query;
}


export {
	getQuery,
	getView,
	camlLog,
	concat
}